// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.product-card {

  $block-name:                &; // #{$block-name}__element

  &__link {
    color: #000;
    text-decoration: none;
    text-align: center;
  }
  &__image {
    margin-bottom: 10px;
  }
  &__name {
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 15px;
  }
  &__caption {
    margin-bottom: 15px;
  }
  &__cost {
    font-weight: 600;
    text-transform: uppercase;
  }
}
