.catalog-header {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  text-align: center;
  margin-bottom: 2em;

  &__title {
    font-family: "Lora", serif;
    font-style: italic;
    font-size: 24px;
    margin-bottom: 0.3em;
    font-weight: 400;
  }

  &__amount-products {
    margin: 0;
    color: #b8b8b8;
  }

}
