//        - sort-filter,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.sort-filter {

  $block-name:                &; // #{$block-name}__element

  position: absolute;
  right: 0;
  top: 10px;
  min-width: 155px;

  // form {
  //   width: 100%;
  // }

  @media (max-width: $screen-lg) {
    position: static;
    max-width: 270px;
    margin-top: 20px;
  }

  @media (max-width: $screen-sm) {
    max-width: 100%;
    width: 100%;
  }

}
