//        - footer-nav,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.footer-nav {

  $block-name:                &; // #{$block-name}__element
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;

  &__item {
    &:not(:first-child) {
      margin-left: 15px;
    }
  }

  &__link {
    display: block;
    text-decoration: none;
    padding: 0.3em;
  }

  @media (max-width: $screen-md) {
    flex-direction: column;
    text-align: center;
    #{$block-name}__item {
      &:not(:first-child) {
        margin-left: 0;
      }
    }
  }

}
